/* Global */
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
/* BreakPoints */
/* Margin to content */
/* Content max-width */
/* Fonts */
/* Text */
/* Colors */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page__body {
  min-width: 320px;
  font-family: "OpenSans", "Arial", serif;
  font-weight: 400;
  font-style: normal;
  color: #000000;
  font-size: 15px;
  line-height: 24px;
  background-color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::after, *::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
}

/* blocks */
.title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1024px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

.sub-title {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63d1bb;
}
@media (min-width: 768px) {
  .sub-title {
    line-height: 17px;
    margin-right: 20px;
  }
}

.main {
  overflow: hidden;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 50px;
  }
  .container--form {
    padding: 0 225px 0 111px;
  }
  .container--about {
    padding: 0 225px;
  }
  .container--review, .container--contact {
    padding: 0 50px 0 225px;
  }
}

.footer {
  margin-top: auto;
  background-color: #f1f1f1;
}

.footer__wrapper {
  padding-bottom: 23px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__wrapper {
    flex-direction: row;
    gap: 30px;
    padding-top: 51px;
    padding-bottom: 58px;
    justify-content: space-between;
  }
}

.footer__logo {
  display: none;
  width: 102px;
  height: 34px;
  color: #000000;
}
@media (min-width: 768px) {
  .footer__logo {
    display: block;
  }
}
.footer__logo:hover {
  color: #46c1ae;
}
.footer__logo:active {
  opacity: 0.3;
}

.footer__logo-img {
  width: 102px;
  height: 34px;
}

.footer__social {
  display: flex;
  list-style-type: none;
  width: 100%;
  justify-content: center;
  gap: 5px 29px;
  padding-bottom: 24px;
  margin: 28px auto 22px auto;
  border-bottom: 1px solid #e5e5e5;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .footer__social {
    padding-bottom: 0;
    margin: 6px 0 0 0;
    border: 0;
    width: unset;
  }
}

.footer__social-item {
  display: flex;
  justify-items: center;
}

.footer__social-icon {
  color: #231f20;
}
.footer__social-icon:hover {
  color: #46c1ae;
}
.footer__social-icon:active {
  opacity: 0.3;
}

.footer__copyright {
  display: flex;
  gap: 27px;
  justify-content: center;
  text-decoration: none;
  color: #000000;
  font-weight: 700;
  font-size: 19px;
  align-items: center;
}
.footer__copyright > .footer__copyright-logo {
  color: #63d1bb;
}
.footer__copyright:hover > .footer__copyright-logo {
  color: #46c1ae;
}
.footer__copyright:active > .footer__copyright-logo {
  opacity: 0.3;
}

.footer__copyright-text {
  line-height: 18px;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.navigation {
  position: relative;
  display: grid;
  grid-template-areas: "logo" "catalog" "additional";
  background-color: #ffffff;
  z-index: 10;
}
@media (min-width: 768px) {
  .navigation {
    grid-template: "logo additional additional" "catalog catalog information";
  }
}
@media (min-width: 1024px) {
  .navigation {
    grid-template: "catalog catalog logo additional additional" "actions actions actions information information";
    grid-template-columns: calc(25% - 37.5px) calc(25% - 37.5px) 150px calc(25% - 37.5px) calc(25% - 37.5px);
    margin: 0 auto;
    width: 100%;
    min-width: 320px;
    max-width: 1150px;
  }
}

.navigation__logo-wrapper {
  border-bottom: 1px solid #ececec;
  width: 100%;
  padding: 22px 0 22px 0;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .navigation__logo-wrapper {
    padding: 0;
    display: block;
    border-bottom: 1px solid #e0e0e0;
  }
}
@media (min-width: 1024px) {
  .navigation__logo-wrapper {
    position: relative;
  }
  .navigation__logo-wrapper::after {
    content: "";
    background-color: #ffffff;
    height: calc(100% + 1px);
    width: 200vw;
    position: absolute;
    left: -50vw;
    z-index: -1;
    border-bottom: 1px solid #e5e5e5;
  }
}

.navigation__logo-link {
  display: inline-block;
  margin-left: -2px;
}
@media (min-width: 768px) {
  .navigation__logo-link {
    margin: 22px 0 13px 48px;
  }
}
@media (min-width: 1024px) {
  .navigation__logo-link {
    margin: 9px 0 0 0;
  }
}
.navigation__logo-link:hover {
  opacity: 0.6;
}
.navigation__logo-link:active {
  opacity: 0.3;
}

.navigation__logo {
  display: block;
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) {
  .navigation__logo {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1024px) {
  .navigation__logo {
    width: 149px;
    height: 72px;
  }
}

.navigation__catalog {
  grid-area: catalog;
  list-style-type: none;
  background-color: #ffffff;
  display: none;
}
.navigation__catalog--visible {
  display: block;
}
@media (min-width: 768px) {
  .navigation__catalog {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: #f9f9f9;
    padding-left: 50px;
    padding-top: 22px;
    gap: 10px 37px;
  }
}
@media (min-width: 1024px) {
  .navigation__catalog {
    background-color: #ffffff;
    padding: 0;
    padding-left: 50px;
    gap: 0;
    border-bottom: 1px solid #e5e5e5;
  }
}

@media (min-width: 1024px) {
  .navigation__catalog-item {
    border-right: 1px solid #e5e5e5;
    padding: 34px 35px 30px 34px;
  }
  .navigation__catalog-item:first-child {
    padding-left: 0;
  }
  .navigation__catalog-item:last-child {
    border: 0;
    padding-right: 10px;
  }
}

.navigation__catalog-link {
  width: 100%;
  padding: 28px 30px 26px 76px;
  border-bottom: 1px solid #ececec;
  display: inline-flex;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-decoration: none;
  color: #000000;
}
.navigation__catalog-link:hover {
  opacity: 0.6;
}
.navigation__catalog-link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__catalog-link {
    border: 0;
    display: block;
    padding: 0;
    font-size: 15px;
    line-height: 24px;
  }
}
@media (min-width: 1024px) {
  .navigation__catalog-link {
    font-size: 17px;
  }
}

.navigation__additional {
  grid-area: additional;
  list-style-type: none;
  background-color: #ffffff;
  height: 100%;
  display: none;
}
.navigation__additional--visible {
  display: block;
}
@media (min-width: 768px) {
  .navigation__additional {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
    padding-right: 50px;
    padding-left: 20px;
    height: auto;
    border-bottom: 1px solid #e0e0e0;
  }
}

@media (min-width: 768px) {
  .navigation__additional-item {
    border-left: 1px solid #e5e5e5;
  }
  .navigation__additional-item--hidden-text {
    margin-right: 32px;
  }
}

@media (min-width: 768px) {
  .navigation__additional-item--hidden-text .navigation__additional-text {
    /*visually-hidden*/
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
  }
}

.navigation__additional-link {
  width: 100%;
  padding: 28px 30px 26px 76px;
  border-bottom: 1px solid #ececec;
  display: inline-flex;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-decoration: none;
  color: #000000;
  position: relative;
}
@media (min-width: 768px) {
  .navigation__additional-link {
    display: flex;
    gap: 31px;
    border-bottom: 0;
    height: auto;
    width: auto;
    padding: 0;
    margin: 33px 0 30px 32px;
  }
}
.navigation__additional-link:hover .navigation__additional-text {
  opacity: 0.6;
}
.navigation__additional-link:hover .navigation__additional-img {
  color: #63d1bb;
  opacity: 1;
}
.navigation__additional-link:active .navigation__additional-text {
  opacity: 0.3;
}
.navigation__additional-link:active .navigation__additional-img {
  color: #63d1bb;
  opacity: 0.3;
}

.navigation__additional-item:last-child .navigation__additional-link {
  margin-right: 0;
}

.navigation__additional-img {
  position: absolute;
  left: 30px;
  opacity: 0.2;
  min-width: 21px;
}
@media (min-width: 768px) {
  .navigation__additional-img {
    position: relative;
    left: auto;
  }
}

.navigation__actions {
  grid-area: actions;
  display: none;
  list-style-type: none;
}
@media (min-width: 1024px) {
  .navigation__actions {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: #f9f9f9;
    gap: 10px 63px;
    padding-left: 50px;
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
    padding-top: 20px;
  }
}

.navigation__actions-link {
  text-decoration: none;
  color: #000000;
}

.navigation__information {
  grid-area: information;
  display: none;
}
@media (min-width: 768px) {
  .navigation__information {
    display: block;
    background-color: #f9f9f9;
    padding-top: 21px;
    padding-left: 20px;
    padding-right: 50px;
    text-align: right;
  }
}

.navigation__toggle {
  position: absolute;
  right: 32px;
  top: 32px;
  cursor: pointer;
  z-index: 1;
  border: 0;
  background-color: transparent;
}
.navigation__toggle::before, .navigation__toggle--is-open::before {
  content: " ";
  background-size: contain;
  display: inline-block;
  background-repeat: no-repeat;
}
.navigation__toggle::before {
  width: 20px;
  height: 14px;
  top: 290px;
  background-image: url("../img/sprite.svg#menu");
}
.navigation__toggle--is-open::before {
  width: 21px;
  height: 20px;
  background-image: url("../img/sprite.svg#cross");
}
.navigation__toggle:hover {
  opacity: 0.6;
}
.navigation__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation--no-js .navigation__toggle {
  display: none;
}

.navigation--no-js .navigation__catalog,
.navigation--no-js .navigation__additional {
  display: block;
}

.intro {
  background-color: #f9f9f9;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 31px;
  padding-top: 72px;
}
@media (min-width: 768px) {
  .intro {
    margin-bottom: 78px;
    padding-top: 0;
  }
}
@media (min-width: 1024px) {
  .intro {
    margin-bottom: 132px;
  }
}
.intro--form {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .intro--form {
    margin-bottom: 65px;
  }
}
@media (min-width: 1024px) {
  .intro--form {
    margin-bottom: 119px;
  }
}
.intro--image {
  background-image: url("../img/content/intro-mobile.jpg");
  background-size: contain;
  background-position: 50% -18px;
  background-repeat: no-repeat;
  padding-top: 73px;
}
@media (min-resolution: 2dppx) {
  .intro--image {
    background-image: url("../img/content/intro-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .intro--image {
    background-image: url("../img/content/intro-tablet.jpg");
    min-height: 746px;
    margin-top: -47px;
    background-position: 50% 0;
    margin-bottom: 53px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .intro--image {
    background-image: url("../img/content/intro-tablet@2x.jpg");
  }
}
@media (min-width: 1024px) {
  .intro--image {
    margin-bottom: 59px;
  }
}

.intro__title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  margin-bottom: 72px;
  max-width: 260px;
}
@media (min-width: 768px) {
  .intro__title {
    font-size: 43px;
    line-height: 50px;
    margin-top: 55px;
    margin-bottom: 76px;
    max-width: 500px;
    padding: 0;
  }
}
@media (min-width: 1024px) {
  .intro__title {
    margin-top: 57px;
    font-size: 53px;
    line-height: 57px;
    max-width: 890px;
    margin-bottom: 93px;
  }
}

.intro--image .intro__title {
  margin-bottom: 120px;
}
@media (min-width: 768px) {
  .intro--image .intro__title {
    margin-top: 83px;
    margin-bottom: 36px;
  }
}
@media (min-width: 1024px) {
  .intro--image .intro__title {
    margin-top: 87px;
  }
}

.intro__navigation {
  margin-top: auto;
  align-items: end;
}
@media (max-width: 767px) {
  .intro__navigation {
    padding: 0;
    margin-bottom: 0;
  }
}

.intro__navigation-list {
  list-style-type: none;
  display: grid;
}
@media (min-width: 768px) {
  .intro__navigation-list {
    grid-template-columns: 1fr 1fr;
  }
}

.intro__navigation-item {
  width: 100%;
  display: flex;
  position: relative;
}
.intro__navigation-item::after {
  content: "";
  position: absolute;
  cursor: pointer;
  background-color: #149080;
  top: 21px;
  right: 27px;
}
.intro__navigation-item:hover, .intro__navigation-item:active {
  margin-top: -5px;
  padding-bottom: 5px;
}
.intro__navigation-item:hover::after {
  opacity: 0.6;
}
.intro__navigation-item:active::after {
  opacity: 0.3;
}
.intro__navigation-item--lamp {
  background-color: #63d1bb;
}
.intro__navigation-item--lamp::after {
  mask: url("../img/sprite.svg#lamp");
  height: 62px;
  width: 40px;
  right: 36px;
}
@media (min-width: 768px) {
  .intro__navigation-item--lamp::after {
    top: 30px;
  }
}
.intro__navigation-item--lamp:hover, .intro__navigation-item--lamp:active {
  background-color: #59d5bc;
}
.intro__navigation-item--brick {
  background-color: #56c3b2;
}
.intro__navigation-item--brick::after {
  mask: url("../img/sprite.svg#brick");
  height: 60px;
  width: 60px;
}
@media (min-width: 768px) {
  .intro__navigation-item--brick::after {
    top: 32px;
    right: 34px;
  }
}
.intro__navigation-item--brick:hover, .intro__navigation-item--brick:active {
  background-color: #46c1ae;
}

.intro__navigation-link {
  width: 100%;
  text-align: start;
  text-decoration: none;
  padding: 24px 160px 31px 30px;
}
@media (min-width: 768px) {
  .intro__navigation-link {
    padding: 25px 160px 34px 36px;
  }
}

.intro__navigation-text {
  color: #ffffff;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  max-width: 130px;
}
@media (min-width: 768px) {
  .intro__navigation-text {
    font-size: 20px;
    line-height: 30px;
  }
}

.week-product {
  padding-bottom: 42px;
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .week-product {
    display: grid;
    grid-template-areas: "heading heading" "description image" "specs image" "price image" "button image";
    grid-template-columns: 1fr 50%;
    grid-template-rows: auto auto auto 1fr auto;
    align-items: start;
    gap: 0 42px;
    padding-bottom: 68px;
    margin-bottom: 57px;
  }
}
@media (min-width: 1024px) {
  .week-product {
    grid-template-areas: "heading heading image" "none description image" "none specs image" "none price image" "none button image";
    grid-template-columns: 117px 1fr 50%;
    gap: 0 58px;
    padding-bottom: 98px;
    margin-bottom: 66px;
  }
}

.week-product__heading {
  grid-area: heading;
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .week-product__heading {
    flex-direction: row-reverse;
    justify-content: space-between;
    gap: 30px;
    margin-bottom: 37px;
  }
}
@media (min-width: 1024px) {
  .week-product__heading {
    grid-area: 1/1/2/-1;
    position: relative;
    z-index: 2;
    flex-direction: row;
    justify-content: start;
    gap: 0;
    margin-top: 74px;
  }
}

.week-product__sub-title {
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .week-product__sub-title {
    max-width: 130px;
    text-align: center;
    margin-bottom: 0;
    margin-top: 6px;
  }
}
@media (min-width: 1024px) {
  .week-product__sub-title {
    width: 89px;
    margin: 9px 43px 0 43px;
    padding-right: 11px;
  }
}

.week-product__description {
  grid-area: description;
  margin-bottom: 21px;
}
@media (min-width: 768px) {
  .week-product__description {
    margin-bottom: 42px;
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1024px) {
  .week-product__description {
    margin-bottom: 52px;
  }
}

.week-product__specs {
  grid-area: specs;
  display: grid;
  grid-template-columns: 38% 62%;
  margin-bottom: 27px;
}
@media (min-width: 768px) {
  .week-product__specs {
    grid-template-columns: 33% 67%;
  }
}

.week-product__specs-point {
  border-bottom: 1px solid #ececec;
  padding-top: 5px;
  padding-bottom: 14px;
  line-height: 20px;
}
.week-product__specs-point:nth-child(odd) {
  padding-right: 10px;
}
@media (min-width: 768px) {
  .week-product__specs-point {
    padding-bottom: 16px;
  }
}

.week-product__image-wrapper {
  grid-area: image;
  margin-bottom: 27px;
}
@media (min-width: 768px) {
  .week-product__image-wrapper {
    margin-bottom: 0;
    margin-top: 13px;
  }
}
@media (min-width: 1024px) {
  .week-product__image-wrapper {
    grid-area: image;
  }
}

.week-product__image {
  width: 100%;
}

.week-product__price {
  grid-area: price;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .week-product__price {
    margin-bottom: 33px;
    align-self: end;
    font-size: 20px;
  }
}

.week-product__button {
  grid-area: button;
  width: 100%;
}

.week-product__title {
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .week-product__title {
    margin-bottom: 0;
  }
}

.button {
  padding: 14px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
  border: 2px solid #444444;
  background-color: #ffffff;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  cursor: pointer;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.button--colored {
  border: 0;
  background-color: #63d1bb;
  color: #ffffff;
  padding: 19px;
  font-size: 17px;
  line-height: 16px;
}
.button--colored:hover {
  background-color: #63d1bb;
  color: #ffffff;
  opacity: 0.6;
}
.button--colored:active {
  background-color: #63d1bb;
  color: #ffffff;
