.modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  min-width: $vp-320;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: transparentize($color-white, 0.1);
  z-index: 10;
}

.modal__for-close {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: -10;
}

.modal.is-open {
  display: flex;
}

.modal__wrapper {
  background-color: $color-white;
  border: 1px solid $color-silver;
  box-shadow: 0 5px 10px transparentize($color-black, 0.75);
  width: 100%;
  padding: 30px 9.4% 47px 9.4%;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 566px;

  @media (min-width: 566px) {
    padding-left: 105px;
    padding-right: 105px;
  }
}

.modal__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 14px;

  @media (min-width: $vp-768) {
    font-size: 30px;
    line-height: 57px;
  }
}

.modal__form-description {
  font-size: 15px;
  line-height: 30px;
  color: $color-charcoal;
  text-align: center;
  margin-bottom: 21px;

  @media (min-width: $vp-768) {
    font-size: 17px;
    line-height: 30px;
  }
}

.modal__form-list {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 7px;
  margin-bottom: 30px;
}

.modal__form-text {
  border: 2px solid transparentize($color-charcoal, 0.9);
  width: 100%;
  display: block;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  color: $color-charcoal;
  padding: 17px 18px 16px 18px;
  font-weight: 700;
  text-align: center;
  cursor: pointer;

  &:hover {
    border-color: transparentize($color-charcoal, 0.7);
  }
}


.modal__form-value:checked + .modal__form-text {
  border-color: $color-downy;
}

.modal__form {
  width: 100%;
  border: 0;
}

.modal__form-button {
  width: 100%;
  font-family: $ff-opensans;
}
